nav {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  font-size: 0.6rem;
  font-weight: 600;
  letter-spacing: 3px;
  flex-wrap: wrap;
  padding: 1.5rem; }
  .nav-icon{
   width: 20px;
   height: 20px;
   padding-right: 5px;
  }
  .nav-icon img{
      height: 100%;
      color: white;
  }
  nav a:not(:last-child) {
    margin-right: 2rem; }
  nav a {
    cursor: pointer;
    display: inline-block;
    color: white;
    text-decoration: none;
    position: relative; 
    margin: 10px 0;
  }
    nav a:before {
      transition: 0.5s all ease;
      position: absolute;
      left: 50%;
      right: 50%;
      bottom: -10px;
      opacity: 0;
      content: "";
      height: 2px;
      background-color: #c0b2fc; }
    nav a:hover {
      color: #c0b2fc; }
      nav a:hover:before {
        transition: 0.5s all ease;
        left: 0;
        right: 0;
        opacity: 1; }
    nav a.selected {
      color: #c0b2fc; }
  nav.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #27263ff2;
    transition: 0.3s; }



