  z-index: 10;
  align-items: center;
  background-color: rgba(0, 0, 0, .5);
  display: flex;
  justify-content: center;
}

.panel {
  background-color: white;
  padding: 16px;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 16px;
  max-height: calc(100% - 32px);
  max-width: 500px;
  z-index: 10;
  border-left: solid 70px transparent; 
  border-right: solid 70px transparent; 
  border-bottom: solid 70px #fff;
  filter: drop-shadow(0 0 30px #333);
  color: black!important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* padlock */
.padlock-container {
  transform: scale(.5);
}

.padlock {
  position: relative;
  width: 260px;
  height: 200px;
  background-image: linear-gradient(
      to bottom right,
      #f2bc23 49.9%,
      #eab02a 50%
  );
  border-radius: 20px 20px 100px 100px;
  margin-top: 125px;
  cursor: pointer;
}

.padlock.unlock::before {
  transform: translate(-50%, -60%);
}

.padlock.unlock::after {
  transform: translate(-50%, calc(-50% - 15px)) rotate(90deg);
}

.padlock::before {
  content: "";
  position: absolute;
  left: 50%;
  z-index: -1;
  width: 200px;
  height: 250px;
  border-radius: 125px;
  border: 40px solid #dbe1e4;
  transform: translate(-50%, -50%);
  transition: transform 300ms cubic-bezier(0.17, 0.67, 0.65, 1.52);
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 65%, 50% 65%, 50% 57%, 22% 57%, 22% 51%, 15% 51%, 14% 52%, 14% 53%, 15% 54%, 22% 54%, 18% 57%, 2% 57%, 0% 55%);
          clip-path: polygon(0% 0%, 100% 0%, 100% 65%, 50% 65%, 50% 57%, 22% 57%, 22% 51%, 15% 51%, 14% 52%, 14% 53%, 15% 54%, 22% 54%, 18% 57%, 2% 57%, 0% 55%);
}

.padlock::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  width: 20px;
  height: 100px;
  background-image: linear-gradient(to right, #ccc 49.9%, #aaa 50%);
  border-radius: 10px;
  transform: translate(-50%, calc(-50% - 15px));
  transition: transform 180ms;
}

.keyhole {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  background-color: #2d3237;
  border-radius: 50%;
  transform: translate(-50%, calc(-50% - 15px));
}

.keyhole::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  background-color: #3d464d;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.keyhole::after {
  content: "";
  position: absolute;
  bottom: -30px;
